.article {
  padding: 22px 0;
}
.article__wrap{
  max-width: 690px;
}
.article__head {
  margin-bottom: 16px;
}
.article__head-svg {
  width: 18%;
}
.article__title {
  margin: 0;
  font-weight: bold;
  font-size: 24px;
  line-height: 120%;
  margin-right: 15px;
}
.article__share {
}
.article__svg {
}
.article__content {
}
.article__support {
}
.article__sub-title {
}
.article__buttons {
  display: flex;
}
.article__button {
  display: inline-flex;
  align-items: center;
  padding: 15px;
  color: inherit;
  text-decoration: none;
  border-radius: 12px;
  &.green {
    background: #36ba5f;
  }
  &.grey {
    background: #f9f9f9;
  }
}
.article__svg {
  &-1 {
    width: 16px;
    height: 16px;
    margin-right: 8px;
  }
  &-2 {
    width: 16px;
    height: 16px;
  }
  &-3 {
    width: 16px;
    height: 12px;
  }
}
